.burger__strip:nth-child(2) {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.burger__strip:nth-child(1),
.burger__strip:nth-child(3) {
  left: 50%;
  transform: translateX(-50%);
}
html {
  line-height: 1.3em;
}
html {
  font-size: 16px;
}
html {
  font-family: museo;
}
html {
  -webkit-font-smoothing: auto;
  -moz-osx-font-smoothing: auto;
}
body {
  min-width: 480px;
  font-weight: 300;
  color: #454545;
  background-image: url("/img/template__bg.png");
}
.no-scroll {
  overflow: hidden;
}
.header {
  background-color: #36374c;
  padding: 16px 20px;
  box-shadow: inset 0 -2px 18px rgba(9,12,18,0.5);
}
.header__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo__img {
  height: 68px;
}
.burger {
  width: 30px;
  height: 18px;
  position: relative;
}
.burger__strip {
  width: 100%;
  height: 4px;
  position: absolute;
  background-color: #fff;
  border-radius: 2px;
}
.burger__strip:nth-child(1) {
  top: 0;
}
.burger__strip:nth-child(3) {
  bottom: 0;
}
@media (min-width: 960px) {
  .burger {
    display: none;
  }
}
.navigation {
  display: none;
}
.navigation__item {
  display: inline-block;
}
.navigation__link {
  display: inline-block;
}
@media (min-width: 960px) {
  .navigation {
    display: block;
  }
}
